import {
  ALERT_RETRIEVE_TRACKER,
  ALERT_RETRIEVE_SEARCH_TRACKER,
} from '../../actions/alerts/Retrieve';
import * as actions from '../../actions/actionTypes';
import { FETCH_OBJECT_STATE } from '../../util/StateConstants';


const defaultState = {
  triggeredAlerts: [],
  alertFetchState: FETCH_OBJECT_STATE.READY,
  alertSortKey: 'last_triggered_time',
  alertSortDir: 'desc',
  refreshInterval: -1,
  alertTimeRange: '-1h',
  alertSideBarDataFetchState: FETCH_OBJECT_STATE.READY,
  alertIDs: [],
  deletedAlertIDs: [],
  alertSPLs: {},
  alertDataPoints: {},
  alertThresholds: {},
  alertManagedByIds: {},
  alertGroupFilters: {},
  alertTypes: {},
  alertsLastFetched: -1,
};

const alertsReducer = (state = defaultState, action) => {
  switch (action.type) {
    case ALERT_RETRIEVE_TRACKER.START: {
      return {
        ...state,
        alertFetchState: FETCH_OBJECT_STATE.IN_PROGRESS,
      };
    }

    case ALERT_RETRIEVE_TRACKER.SUCCESS: {
      if (action.response) {
        const { data, updated } = action.response;
        const triggeredAlerts = data;
        return {
          ...state,
          triggeredAlerts,
          alertFetchState: FETCH_OBJECT_STATE.SUCCESS,
          alertsLastFetched: updated,
        };
      }
      return state;
    }

    case ALERT_RETRIEVE_TRACKER.FAILURE: {
      return { ...state, alertFetchState: FETCH_OBJECT_STATE.ERROR };
    }

    case ALERT_RETRIEVE_SEARCH_TRACKER.START: {
      return {
        ...state,
        alertSideBarDataFetchState: FETCH_OBJECT_STATE.IN_PROGRESS,
      };
    }

    case ALERT_RETRIEVE_SEARCH_TRACKER.SUCCESS: {
      if (action.response) {
        const {
          alertIDs,
          alertSPLs,
          alertDataPoints,
          alertManagedByIds,
          alertGroupFilters,
          alertTypes,
          deletedAlertIDs,
          alertThresholds,
        } = action.response;
        return {
          ...state,
          alertIDs,
          alertSPLs,
          alertDataPoints,
          alertManagedByIds,
          alertGroupFilters,
          alertTypes,
          deletedAlertIDs,
          alertThresholds,
          alertSideBarDataFetchState: FETCH_OBJECT_STATE.SUCCESS,
        };
      }
      return state;
    }

    case ALERT_RETRIEVE_SEARCH_TRACKER.FAILURE: {
      return {
        ...state,
        alertIDs: defaultState.alertIDs,
        alertSPLs: defaultState.alertSPLs,
        alertDataPoints: defaultState.alertDataPoints,
        alertManagedByIds: defaultState.alertManagedByIds,
        alertGroupFilters: defaultState.alertGroupFilters,
        alertTypes: defaultState.alertTypes,
        alertSideBarDataFetchState: FETCH_OBJECT_STATE.ERROR,
      };
    }

    case actions.ALERT_LISTING_SORT_ALERTS: {
      const prevSortDir = state.alertSortKey === action.sortKey ? state.alertSortDir : 'none';
      const nextSortDir = prevSortDir === 'asc' ? 'desc' : 'asc';
      return {
        ...state,
        alertSortKey: action.sortKey,
        alertSortDir: nextSortDir,
      };
    }

    case actions.UPDATE_REFRESH_INTERVAL: {
      return {
        ...state,
        refreshInterval: action.interval,
      };
    }

    case actions.ALERT_TIME_RANGE_UPDATE: {
      return {
        ...state,
        alertTimeRange: action.timeRange || defaultState.alertTimeRange,
      };
    }

    default:
      return state;
  }
};

export default alertsReducer;



