import React from 'react';
import PropTypes from 'prop-types';
import { find } from 'lodash';
import Button from '@splunk/react-ui/Button';
import Dropdown from '@splunk/react-ui/Dropdown';
import Menu from '@splunk/react-ui/Menu';


// eslint-disable-next-line react/prop-types
const renderMenuItem = (onSelect, selected) => ({ label, value }) => (
  <Menu.Item
    key={value}
    data-value={value}
    selected={selected === value}
    autoFocus={selected === value}
    onClick={() => onSelect(value)}
    selectable
    style={{ display: 'flex', whiteSpace: 'nowrap' }}
  >
    {label}
  </Menu.Item>
);

const IconDropdown = (props) => {
  const {
    icon,
    items = [],
    selected,
    placeholder,
    prefixLabel,
    disabled,
    appearance,
    scrollContainer,
    buttonStyle,
    dropdownStyle,
  } = props;

  const selection = find(items, ({ value }) => value === selected);
  const selectedLabel = selection && selection.label;
  const toggleLabel = (selectedLabel && `${prefixLabel || ''}${selectedLabel}`) || placeholder;
  const toggle = (
    <Button
      style={buttonStyle}
      appearance={appearance}
      label={toggleLabel}
      icon={icon}
      isMenu
      disabled={disabled}
    />
  );

  return (
    <Dropdown
      style={{ maxWidth: '100%', ...dropdownStyle }}
      toggle={toggle}
      scrollContainer={scrollContainer}
    >
      {({ maxHeight }) => (
        <Menu
          stopScrollPropagation
          style={{ minWidth: '7.5rem', maxHeight, overflowY: 'auto' }}
        >
          {items.map(renderMenuItem(props.onSelect, selected))}
        </Menu>
      )}
    </Dropdown>
  );
};

const ItemType = PropTypes.shape({
  label: PropTypes.string.isRequired,
  value: PropTypes.string,
});

IconDropdown.propTypes = {
  icon: PropTypes.node,
  onSelect: PropTypes.func,
  selected: PropTypes.string,
  dropdownStyle: PropTypes.object,
  buttonStyle: PropTypes.object,
  scrollContainer: PropTypes.object,
  disabled: PropTypes.bool,
  items: PropTypes.arrayOf(ItemType),
  placeholder: PropTypes.string,
  prefixLabel: PropTypes.string,
  appearance: PropTypes.string,
};

export default IconDropdown;



