import React from 'react';
import PropTypes from 'prop-types';
import Highcharts from '@splunk/highcharts';
import annotationsPlugin from '@splunk/highcharts-annotations';
import splunkMoment from '@splunk/moment';
import timeAxisPlugin from '@splunk/highcharts-time-axis';
import _ from 'lodash';
import { THRESHOLD_DIRECTION } from '../../util/StateConstants';
import { encodeThresholds } from '../../util/splUtils';
import { WARNING_BAND, CRITICAL_BAND } from '../common/Colors';

const { useSplunkMoment } = timeAxisPlugin(Highcharts);
useSplunkMoment(splunkMoment);

annotationsPlugin(Highcharts);

export default class Chart extends React.Component {
  static propTypes = {
    /**
     * ID/Name of the chart
     * must be specified if showing multiple charts on the same page
     */
    chartName: PropTypes.string,
    /**
     * style for the chart contianer
     */
    style: PropTypes.object,
    /*
      fields are aggregations.
      example: Avg, Max, Min, StdDev etc...

      columns are array of values for each aggregation. The first column is always
      _time (s).

      [['152024000', '152074000', ...], ['1', '2', ...], ['10', '20', ...]]
    */
    chartData: PropTypes.shape({
      fields: PropTypes.arrayOf(PropTypes.string),
      columns: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
      annotations: PropTypes.arrayOf(PropTypes.shape({
        value: PropTypes.number,
        label: PropTypes.string,
        color: PropTypes.string,
      })),
    }).isRequired,

    /*
      warningValue: value set by user for warning threshold in the dialog
      criticalValue: value set by user for critical threshold in the dialog
      direction: greater than/ lesser than
    */
    plotBandsData: PropTypes.shape({
      warningValue: PropTypes.string,
      criticalValue: PropTypes.string,
      direction: PropTypes.oneOf(Object.values(THRESHOLD_DIRECTION)),
    }),
  };

  static defaultProps = {
    chartName: '',
    style: {},
    chartData: {
      fields: [],
      columns: [],
      annotations: [],
    },
    plotBandsData: {
      warningValue: '',
      criticalValue: '',
      direction: THRESHOLD_DIRECTION.NORMAL,
    },
  };

  constructor(props) {
    super(props);
    this.chartContainerName = `chart-container-${this.props.chartName}`;
    this.chart = null;
  }

  shouldComponentUpdate(nextProps) {
    if (_.isEqual(this.props.plotBandsData, nextProps.plotBandsData) &&
        _.isEqual(this.props.chartData, nextProps.chartData)) {
      return false;
    }
    return true;
  }

  componentWillUnmount() {
    if (this.chart) {
      this.chart.destroy();
    }
  }

  drawChart(series, plotBandsData) {
    this.chart = Highcharts.chart(this.chartElement, {
      credits: {
        enabled: false,
      },
      legend: {
        align: 'right',
        verticalAlign: 'top',
        layout: 'vertical',
      },
      chart: {
        height: 160,
        width: 500,
        type: 'line',
      },
      title: {
        text: '',
      },
      xAxis: {
        type: 'datetime',
        annotations: this.props.chartData.annotations || [],
      },
      yAxis: {
        plotBands: [{
          color: WARNING_BAND,
          from: plotBandsData.warning_min,
          to: plotBandsData.warning_max,
          label: 'Medium',
        }, {
          color: CRITICAL_BAND,
          from: plotBandsData.critical_min,
          to: plotBandsData.critical_max,
          label: 'Critical',
        }],
        title: {
          text: null,
        },
      },
      plotOptions: {
        series: {
          connectNulls: true,
        },
      },
      series,
    });
  }

  chartRef(element) {
    if (!this.chartElement) {
      this.chartElement = element;
    }
    let series = null;
    let plotBandsData = null;
    if (this.props.chartData.columns.length) {
      const timeSeries = this.props.chartData.columns[0].map(each => each * 1000);
      series = _.map(this.props.chartData.columns.slice(1), (column, index) => ({
        data: _.map(column, (point, idx) => [timeSeries[idx], point && parseFloat(point)]),
        name: this.props.chartData.fields[index + 1],
        animation: false,
      }));
      plotBandsData = encodeThresholds({ ...this.props.plotBandsData });
    }
    if (series && plotBandsData) {
      this.drawChart(series, plotBandsData);
    }
  }

  render() {
    return (
      <div>
        <div id={this.chartContainerName} ref={el => this.chartRef(el)} style={this.props.style} />
      </div>
    );
  }
}



