import {
  ALERT_DELETE,
  ALERT_DELETION_CONFIRM,
  ALERT_DELETION_READY,
  START,
  SUCCESS,
  FAILURE,
} from '../actionTypes';
import ActionUtils from '../actionUtils';

export const ALERT_DELETE_TRACKER = ActionUtils.createRequestTypes(ALERT_DELETE);

export const alertDeleteTracker = {
  start: () => ActionUtils.createAction(ALERT_DELETE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(ALERT_DELETE_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(ALERT_DELETE_TRACKER[FAILURE], { error }),
};

export const deleteAlert = data => ({
  type: ALERT_DELETE,
  data,
});

export const alertDeletionConfirm = alertName => ({
  type: ALERT_DELETION_CONFIRM,
  alertName,
});

export const alertDeletionReady = () => ({
  type: ALERT_DELETION_READY,
});



