import {
  ALERT_THRESHOLDS_RETRIEVE,
  ALERT_CHART_DATA_POINTS_RETRIEVE,
  ALERT_RETRIEVE,
  ALERT_LISTING_SORT_ALERTS,
  ALERT_UPSERT_READY,
  ALERT_RETRIEVE_READY,
  ALERT_DATA_POINTS_RETRIEVE_READY,
  ALERT_TIME_RANGE_UPDATE,
  ALERT_RETRIEVE_SEARCH,
  START,
  SUCCESS,
  FAILURE,
} from '../actionTypes';
import ActionUtils from '../actionUtils';

export const ALERT_RETRIEVE_TRACKER = ActionUtils.createRequestTypes(ALERT_RETRIEVE);
export const ALERT_THRESHOLDS_RETRIEVE_TRACKER = ActionUtils.createRequestTypes(
  ALERT_THRESHOLDS_RETRIEVE);
export const ALERT_CHART_DATA_POINTS_RETRIEVE_TRACKER = ActionUtils.createRequestTypes(
  ALERT_CHART_DATA_POINTS_RETRIEVE);
export const ALERT_RETRIEVE_SEARCH_TRACKER = ActionUtils.createRequestTypes(ALERT_RETRIEVE_SEARCH);

export const alertThresholdsRetrieveTracker = {
  start: () => ActionUtils.createAction(ALERT_THRESHOLDS_RETRIEVE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(
    ALERT_THRESHOLDS_RETRIEVE_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(
    ALERT_THRESHOLDS_RETRIEVE_TRACKER[FAILURE], { error }),
};

export const alertChartDataPointsRetrieveTracker = {
  start: () => ActionUtils.createAction(ALERT_CHART_DATA_POINTS_RETRIEVE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(
    ALERT_CHART_DATA_POINTS_RETRIEVE_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(
    ALERT_CHART_DATA_POINTS_RETRIEVE_TRACKER[FAILURE], { error }),
};

export const alertRetrieveTracker = {
  start: () => ActionUtils.createAction(ALERT_RETRIEVE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(ALERT_RETRIEVE_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(ALERT_RETRIEVE_TRACKER[FAILURE], { error }),
};

export const alertRetrieveSearchTracker = {
  start: () => ActionUtils.createAction(ALERT_RETRIEVE_SEARCH_TRACKER[START], {}),
  success: response => ActionUtils.createAction(
    ALERT_RETRIEVE_SEARCH_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(ALERT_RETRIEVE_SEARCH_TRACKER[FAILURE], { error }),
};

export const loadAlert = alertName => ({
  type: ALERT_THRESHOLDS_RETRIEVE,
  alertName,
});

export const loadChartDataPoints = (search, earliest) => ({
  type: ALERT_CHART_DATA_POINTS_RETRIEVE,
  search,
  earliest,
});

export const loadAlerts = data => ActionUtils.createAction(ALERT_RETRIEVE, data);

export const sortAlerts = sortKey => ({
  type: ALERT_LISTING_SORT_ALERTS,
  sortKey,
});

export const alertUpsertReady = () => ({
  type: ALERT_UPSERT_READY,
});

export const alertRetrieveReady = () => ({
  type: ALERT_RETRIEVE_READY,
});

export const alertDataPointsRetrieveReady = () => ({
  type: ALERT_DATA_POINTS_RETRIEVE_READY,
});

export const alertTimeRangeUpdate = timeRange => ({
  type: ALERT_TIME_RANGE_UPDATE,
  timeRange,
});

export const entityClickGetAlerts = (entityTitle, alertIDs) => ({
  type: ALERT_RETRIEVE_SEARCH,
  entityTitle,
  alertIDs: Array.isArray(alertIDs) ? alertIDs : [alertIDs],
});



