import React from 'react';
import PropTypes from 'prop-types';
import { pick } from 'lodash';
import SidePanel from '@splunk/react-ui/SidePanel';
import Heading from '@splunk/react-ui/Heading';
import CloseButton from '@splunk/react-ui/CloseButton';
import Warning from '@splunk/react-icons/Warning';
import Error from '@splunk/react-icons/Error';
import InfoCircle from '@splunk/react-icons/InfoCircle';
import { createTestHook } from '../../util/TestSupport';
import { UNHEALTHY_HOVER, UNKNOWN_FLAT, WARNING_ICON, HEALTHY_HOVER } from '../common/Colors';
import AlertSidebarChartArea from './AlertSidebarChartArea';

export default class AlertSidebar extends React.Component {
  static propTypes = {
    /** type of object */
    objectType: PropTypes.oneOf(['entity', 'group']).isRequired,
    /** id of object */
    objectId: PropTypes.string.isRequired,
    /** Title of object to be displayed */
    objectTitle: PropTypes.string.isRequired,
    /** filter of the object */
    objectFilter: PropTypes.object,
    /** identifer dimension of object */
    objectIdDimension: PropTypes.string,
    /** Number of Critical, Warning, and Info alerts */
    alertCountBreakdown: PropTypes.shape({
      Critical: PropTypes.string,
      Warning: PropTypes.string,
      Info: PropTypes.string,
    }).isRequired,
    /** Current most severe open alert */
    currentMostSevereAlert: PropTypes.number.isRequired,
    /** Boolean to determine if sidebar is displayed */
    open: PropTypes.bool.isRequired,
    /** Function to close the sidebar */
    onRequestClose: PropTypes.func.isRequired,
    /** Chart area related props */
    ...AlertSidebarChartArea.propTypes,
  };

  static defaultProps = {
    /** default props for chart area */
    ...AlertSidebarChartArea.defaultProps,
  }

  constructor(props) {
    super(props);
    this.styles = {
      headerStyle: {
        width: '100%',
        padding: '10px',
        boxSizing: 'border-box',
        backgroundColor: UNKNOWN_FLAT,
        display: 'table',
        position: 'relative',
      },
      headingTextStyle: {
        color: 'white',
        marginTop: '10px',
      },
      alertIconStyle: {
        marginRight: '5px',
        marginBottom: '2px',
        fontSize: '20px',
      },
      iconMargin: {
        marginRight: '20px',
      },
    };
  }

  getAlertColor() {
    switch (this.props.currentMostSevereAlert) {
      case 1:
        return HEALTHY_HOVER;
      case 3:
        return WARNING_ICON;
      case 5:
        return UNHEALTHY_HOVER;
      default:
        return UNKNOWN_FLAT;
    }
  }

  render() {
    const chartAreaProps = pick(this.props, Object.keys(AlertSidebarChartArea.propTypes));
    return (
      <SidePanel
        open={this.props.open}
        dockPosition="right"
        onRequestClose={this.props.onRequestClose}
        useLayerForClickAway={false}
        {...createTestHook(__filename, name)}
        innerStyle={{ width: 550 }}
      >
        <div style={{ ...this.styles.headerStyle, backgroundColor: this.getAlertColor() }}>
          <CloseButton
            style={{ position: 'absolute', right: 0, top: 0, color: 'white' }}
            onClick={this.props.onRequestClose}
          />
          <Heading style={this.styles.headingTextStyle}>
            {this.props.objectTitle}
          </Heading>
          <div
            id="alert-info-breakdown"
            style={{ color: 'white' }}
          >
            <span id="critical-count" style={this.styles.iconMargin}>
              <Error style={this.styles.alertIconStyle} />
              {this.props.alertCountBreakdown.Critical || 0}
            </span>
            <span id="warning-count" style={this.styles.iconMargin}>
              <Warning style={this.styles.alertIconStyle} />
              {this.props.alertCountBreakdown.Medium || 0}
            </span>
            <span id="info-count" style={this.styles.iconMargin}>
              <InfoCircle style={this.styles.alertIconStyle} />
              {this.props.alertCountBreakdown.Info || 0}
            </span>
          </div>
        </div>
        <AlertSidebarChartArea
          {...chartAreaProps}
        />
      </SidePanel>
    );
  }
}



