import React from 'react';
import pick from 'lodash/pick';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {
  loadAlerts,
  sortAlerts,
  alertTimeRangeUpdate,
  entityClickGetAlerts,
} from '../../actions/alerts/Retrieve';
import updateRefreshInterval from '../../actions/common/Refresh';
import EntityCentricAlertsList from '../../components/alerts/EntityCentricAlertsListing';

class AlertFeedContainer extends React.Component {
  static propTypes = {
    /** Callback function to fetch triggered alerts */
    loadAlerts: PropTypes.func.isRequired,
    /**
     * import propTypes of EntityCentricAlertsList component
     */
    ...EntityCentricAlertsList.PropTypes,
  };

  static defaultProps = { ...EntityCentricAlertsList.defaultProps };

  componentDidMount() {
    this.props.loadAlerts();
  }

  render() {
    const alertsProps = pick(this.props, Object.keys(EntityCentricAlertsList.propTypes));
    return (
      <div>
        <EntityCentricAlertsList {...alertsProps} />
      </div>
    );
  }
}

const mapStatesToProps = state => ({
  ...state.alertsReducer,
});

const mapDispatchToProps = dispatch => bindActionCreators({
  loadAlerts,
  onAlertSort: sortAlerts,
  onEntityClickGetAlerts: entityClickGetAlerts,
  onRefreshAlertEntities: loadAlerts,
  onUpdateRefreshInterval: updateRefreshInterval,
  onAlertTimeRangeUpdate: alertTimeRangeUpdate,
}, dispatch);

export default connect(
  mapStatesToProps,
  mapDispatchToProps,
)(AlertFeedContainer);



