import React from 'react';
import PropTypes from 'prop-types';
import Table from '@splunk/react-ui/Table';
import Heading from '@splunk/react-ui/Heading';
import P from '@splunk/react-ui/Paragraph';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import Link from '@splunk/react-ui/Link';
import { _ } from '@splunk/ui-utils/i18n';
import { zipObject } from 'lodash';
import BoxFilled from '@splunk/react-icons/BoxFilled';
import TriangleUp from '@splunk/react-icons/TriangleUp';
import CircleFilled from '@splunk/react-icons/CircleFilled';
import { createTestHook } from '../../util/TestSupport';
import {
  ENTITY_CENTRIC_ALERT_INFO_FIELDS_MAP,
  FETCH_OBJECT_STATE,
} from '../../util/StateConstants';
import { HEALTHY_HOVER, WARNING_ICON, CRITICAL_ICON } from '../common/Colors';
import TagPill from '../common/TagPill';
import EMUtils from '../../libs/EMUtils';
import AlertSidebar from './AlertSidebar';
import RefreshControl from '../common/RefreshControl';
import TimeRange, { rangeChoices } from '../common/TimeRange';
import Divider from '../common/Divider';
import AlertSidebarChartArea from './AlertSidebarChartArea';

export default class EntityCentricAlertsList extends React.Component {
  static propTypes = {
    /** List of alerts to be displayed */
    triggeredAlerts: PropTypes.array,
    /** The key to sort the alerts by */
    alertSortKey: PropTypes.oneOf(['entity_title', 'count', 'last_triggered_time', 'alert_total_count', 'most_severe']),
    /** The order of sorting: asc, desc */
    alertSortDir: PropTypes.oneOf(['asc', 'desc']),
    /** Callback for when an alert is to be sorted */
    onAlertSort: PropTypes.func.isRequired,
    /** Callback for when a entity is clicked */
    onEntityClickGetAlerts: PropTypes.func,
    /** Auto-refresh interval */
    refreshInterval: PropTypes.number,
    /** Callback to update refresh interval */
    onRefreshAlertEntities: PropTypes.func.isRequired,
    /** Callback to update refresh interval */
    onUpdateRefreshInterval: PropTypes.func.isRequired,
    /** State of the alert entity fetch */
    alertFetchState: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
    /** Callback for changing time range picker */
    onAlertTimeRangeUpdate: PropTypes.func.isRequired,
    /** Time control for alerts */
    alertTimeRange: PropTypes.oneOf(Object.keys(rangeChoices)).isRequired,
    /** Alert last fetched timestamp */
    alertsLastFetched: PropTypes.number,

    alertSideBarDataFetchState: PropTypes.string,
    /** Chart area related props */
    ...AlertSidebarChartArea.propTypes,
  };

  static defaultProps = {
    triggeredAlerts: [],
    alertSortKey: 'entity_title',
    alertSortDir: 'desc',
    onEntityClickGetAlerts: null,
    refreshInterval: -1,
    alertFetchState: FETCH_OBJECT_STATE.READY,
    alertSideBarDataFetchState: FETCH_OBJECT_STATE.READY,
    alertsLastFetched: -1,
    ...AlertSidebarChartArea.defaultProps,
  };

  constructor(props) {
    super(props);
    this.alertInfoFieldKeys = Array.from(ENTITY_CENTRIC_ALERT_INFO_FIELDS_MAP.keys());
    this.alertInfoFieldValues = Array.from(ENTITY_CENTRIC_ALERT_INFO_FIELDS_MAP.values());
    this.state = {
      sideBarOpen: false,
      sideBarEntityTitle: '',
      sideBarAlertCountBreakdown: {},
      sideBarMostSevere: 0,
    };
    this.styles = {
      heading: {
        marginTop: '20px',
        marginLeft: '20px',
      },
      tagPill: {
        marginRight: '5px',
        marginBottom: '5px',
        display: 'inline-block',
      },
      navContainer: {
        height: '100%',
        width: '100%',
        display: 'flex',
        alignItems: 'center',
      },
      entitiesCountStyle: {
        marginLeft: '20px',
        marginTop: '10px',
        marginRight: '10px',
      },
      severityContainer: {
        display: 'flex',
        alignItems: 'center',
      },
      divider: {
        display: 'flex',
        alignItems: 'center',
        marginRight: '10px',
      },
    };
  }

  onTimeRangeChanged = (timeRange) => {
    this.props.onAlertTimeRangeUpdate(timeRange);
  }

  /**
   * Returns the alerts table body
   *
   * @memberOf EntityCentricAlertsListingPageContent
   */
  getTableBody = () => (
    <Table.Body>
      {this.props.triggeredAlerts.map((alertRow, idx) => {
        const entityTitle = alertRow.entity_title;
        const entityDimensions = alertRow.dimensions;
        const entityIdDimensionName = alertRow.identifier_dimensions;
        const entityId = alertRow.entity_id;
        const alertCount = alertRow.alert_total_count;
        const mostSevere = alertRow.most_severe ? Number(alertRow.most_severe) : 0;
        const lastTriggered = alertRow.last_triggered_time;
        const alertCountBreakdown = alertRow.severity_info;
        const metricName = alertRow.latest_metric_name;
        const metricStateChange = alertRow.state_change;
        const alertMetricNames = alertRow.alert_metric_names instanceof Array ?
          alertRow.alert_metric_names :
          [alertRow.alert_metric_names];
        const alertIds = alertRow.alert_ids instanceof Array ?
          alertRow.alert_ids :
          [alertRow.alert_ids];

        return (
          // eslint-disable-next-line
          <Table.Row key={idx}>
            <Table.Cell>
              <Link onClick={() =>
                this.openAlertSidebar(
                  entityTitle,
                  alertCountBreakdown,
                  mostSevere,
                  entityDimensions,
                  entityId,
                  entityIdDimensionName,
                  alertMetricNames,
                  alertIds,
                )}
              >
                {entityTitle}
              </Link>
            </Table.Cell>
            <Table.Cell>{this.translateSeverity(mostSevere)}</Table.Cell>
            <Table.Cell>{alertCount}</Table.Cell>
            <Table.Cell>{lastTriggered}</Table.Cell>
            <Table.Cell>{`${metricName} ${this.stateToPastTense(metricStateChange)}`}</Table.Cell>
            <Table.Cell>
              {EMUtils.dimesionObjectToArray(entityDimensions).map((dimension) => {
                const [key, value] = dimension.split(':');
                return (
                  <TagPill
                    className="dimension"
                    style={this.styles.tagPill}
                    key={dimension}
                    data={{ type: key, name: value }}
                    isRemoveable={false}
                  />
                );
              })}
            </Table.Cell>
          </Table.Row>
        );
      })}
    </Table.Body>
  );

  /**
   * Returns the alerts table header
   *
   * @memberOf EntityCentricAlertsListingPageContent
   */
  getTableHead = () => {
    const [
      entityKey,
      alertCountKey,
      triggeredTimeKey,
      mostSevereKey,
    ] = this.alertInfoFieldKeys;
    const [
      entityTitleFieldName,
      alertCountFieldName,
      triggeredTimeFieldName,
      mostSevereFieldName,
      lastTriggeredMetricFieldName,
    ] = this.alertInfoFieldValues;

    return (
      <Table.Head>
        <Table.HeadCell
          key={entityKey}
          onSort={this.handleSort}
          sortKey={entityKey}
          sortDir={entityKey === this.props.alertSortKey ? this.props.alertSortDir : 'none'}
        >
          {entityTitleFieldName}
        </Table.HeadCell>
        <Table.HeadCell
          key={mostSevereKey}
          onSort={this.handleSort}
          sortKey={mostSevereKey}
          sortDir={mostSevereKey === this.props.alertSortKey ? this.props.alertSortDir : 'none'}
        >
          {mostSevereFieldName}
        </Table.HeadCell>
        <Table.HeadCell
          key={alertCountKey}
          onSort={this.handleSort}
          sortKey={alertCountKey}
          sortDir={alertCountKey === this.props.alertSortKey ? this.props.alertSortDir : 'none'}
        >
          {alertCountFieldName}
        </Table.HeadCell>
        <Table.HeadCell
          key={triggeredTimeKey}
          onSort={this.handleSort}
          sortKey={triggeredTimeKey}
          sortDir={triggeredTimeKey === this.props.alertSortKey ? this.props.alertSortDir : 'none'}
        >
          {triggeredTimeFieldName}
        </Table.HeadCell>
        <Table.HeadCell>{lastTriggeredMetricFieldName}</Table.HeadCell>
        <Table.HeadCell>{_('Dimensions')}</Table.HeadCell>
      </Table.Head>
    );
  }

  // Only meant to be used with "improve" and "degrade"
  stateToPastTense = word => `${word}d`;

  translateSeverity = (severity) => {
    switch (severity) {
      case 5:
        return (<span style={this.styles.severityContainer}>
          <BoxFilled style={{ color: CRITICAL_ICON, paddingRight: '8px' }} />{_('Critical')}
        </span>);
      case 3:
        return (<span style={this.styles.severityContainer}>
          <TriangleUp style={{ color: WARNING_ICON, paddingRight: '6px' }} /> {_('Medium')}
        </span>);
      case 1:
        return (<span style={this.styles.severityContainer}>
          <CircleFilled style={{ color: HEALTHY_HOVER, paddingRight: '8px' }} /> {_('Info')}
        </span>);
      default:
        return '-';
    }
  }

  /**
   * Handles opening of the alert sidebar
   * @memberOf EntityCentricAlertsListingPageContent
   */
  openAlertSidebar = (
    entityTitle,
    alertCountBreakdown,
    alertMostSevere,
    entityDimensions,
    entityId,
    entityIdDimensionName,
    alertMetricNames,
    alertIds,
  ) => {
    this.props.onEntityClickGetAlerts(entityTitle, alertIds);
    const alertMetricNamesMap = zipObject(alertIds, alertMetricNames);
    this.setState({
      sideBarOpen: true,
      sideBarEntityTitle: entityTitle,
      sideBarEntityFilter: entityDimensions,
      sideBarAlertCountBreakdown: alertCountBreakdown,
      sideBarMostSevere: alertMostSevere,
      sideBarEntityId: entityId,
      sideBarEntityIdDimensionName: entityIdDimensionName,
      sideBarAlertMetricNames: alertMetricNamesMap,
      sideBarAlertIds: alertIds,
    });
  }

  /**
   * Handles closing of the alert sidebar
   *
   * @memberOf EntityCentricAlertsListingPageContent
   */
  closeAlertSidebar = () => {
    this.setState({
      sideBarOpen: false,
      sideBarEntityTitle: '',
      sideBarEntityFilter: {},
      sideBarAlertCountBreakdown: {},
      sideBarMostSevere: 0,
    });
  };

  /**
   * Handles sorting of the entity centric alert listing table
   *
   * @memberOf EntityCentricAlertsListingPageContent
   */
  handleSort = (e, { sortKey }) => {
    this.props.onAlertSort(sortKey);
  };

  render() {
    const entityCount = this.props.triggeredAlerts.length;
    const entityVerbiage = (entityCount === 1 ? _(`${entityCount} entity`) :
      _(`${entityCount} entities`));
    return (
      <div {...createTestHook(__filename, name)}>
        <Heading style={this.styles.heading} level={1}>{_('Alerts')}</Heading>
        <div style={this.styles.navContainer}>
          <P style={this.styles.entitiesCountStyle} data-test="entity-count">
            {entityVerbiage}
          </P>
          <div style={this.styles.divider}>
            <Divider />
          </div>
          <TimeRange
            onTimeRangeChanged={this.onTimeRangeChanged}
            alertTimeRange={this.props.alertTimeRange}
          />
          <RefreshControl
            refreshInterval={this.props.refreshInterval}
            onRefresh={this.props.onRefreshAlertEntities}
            onUpdateRefreshInterval={this.props.onUpdateRefreshInterval}
            mainStyles={{ paddingLeft: '20px' }}
          />
          {this.props.alertFetchState === FETCH_OBJECT_STATE.IN_PROGRESS &&
            <WaitSpinner />
          }
        </div>
        <AlertSidebar
          open={this.state.sideBarOpen}
          onRequestClose={this.closeAlertSidebar}
          objectType={'entity'}
          objectId={this.state.sideBarEntityId}
          objectIdDimension={this.state.sideBarEntityIdDimensionName}
          objectTitle={this.state.sideBarEntityTitle}
          objectFilter={this.state.sideBarEntityFilter}
          alertCountBreakdown={this.state.sideBarAlertCountBreakdown}
          currentMostSevereAlert={this.state.sideBarMostSevere}
          alertSPLs={this.props.alertSPLs}
          alertIDs={this.props.alertIDs}
          alertDataPoints={this.props.alertDataPoints}
          alertThresholds={this.props.alertThresholds}
          alertMetricNames={this.state.sideBarAlertMetricNames}
          alertTypes={this.props.alertTypes}
          alertGroupFilters={this.props.alertGroupFilters}
          alertManagedByIds={this.props.alertManagedByIds}
          deletedAlertIDs={this.props.deletedAlertIDs}
          alertSideBarDataFetchState={this.props.alertSideBarDataFetchState}
        />
        <Table stripeRows data-updated={this.props.alertsLastFetched}>
          {this.getTableHead()}
          {this.getTableBody()}
        </Table>
      </div>
    );
  }
}



