import SearchJob from '@splunk/search-job';
import EMUtils from '../libs/EMUtils';
import { requestAPI } from './util';
import { ALERT_SEVERITY_LEVEL_BY_KEY } from '../util/StateConstants';


const BASE_URL = EMUtils.getAppSplunkdUrl('entity_monitoring/alert');
const CHART_BASE_URL = EMUtils.getAppSplunkdUrl('search/jobs');

const getMostSeverByEntitySubsearch = `| search index="infra_alerts" current_state != "None"
      | rex field=_raw "managed_by_id=\\"(?<managed_by_id>.*?)\\","
      | rex field=_raw "entity_id=\\"(?<entity_id>.*?)\\","
      | stats latest(current_state) as most_severe, latest(metric_name) as latest_metric_name, latest(state_change) as state_change by entity_id, ss_id
      | sort entity_id, -most_severe
      | dedup entity_id`;

export const getEntityCentricAlerts = (sortKey, sortDir, timeRange) => {
  const sorting = sortDir === 'none' ? '' : `sort ${sortKey} ${sortDir}`;
  const entityCentricAlertSearchJob = SearchJob.create({
    search: `index="infra_alerts" current_state != "None"
      | rex field=_raw "managed_by_id=\\"(?<managed_by_id>.*?)\\","
      | rex field=_raw "entity_id=\\"(?<entity_id>.*?)\\","
      | stats count(state_change) as count, latest(_time) as latest_by_state, list(managed_by_id) as managed_by_ids, list(managed_by_type) as managed_by_types, list(ss_id) as ss_ids, list(metric_name) as alert_metric_names, values(entity_title) as entity_title, values(identifier_dimensions) as identifier_dimensions by entity_id, current_state
      | eval alert_types_and_ids=mvdedup(mvzip(mvzip(mvzip(managed_by_types, managed_by_ids, "~"), ss_ids, "~"), alert_metric_names, "~"))
      | stats list(current_state) as severity, list(count) as count_by_severity, sum(count) as alert_total_count, max(latest_by_state) as last_triggered_time, values(alert_types_and_ids) as alert_types_and_ids, values(entity_title) as entity_title, values(identifier_dimensions) as identifier_dimensions by entity_id
      | rex field=alert_types_and_ids "(?<managed_by_type>\\S+)~(?<managed_by_id>\\S+)~(?<alert_ids>\\S+)~(?<alert_metric_names>\\S+)"
      | fields - alert_types_and_ids
      | eval severity_info = mvzip(severity, count_by_severity, ":")
      | fields - severity, count_by_severity
      | join type=inner max=0 entity_id [
      ${getMostSeverByEntitySubsearch}
      ]
      | ${sorting}`,
    earliest_time: timeRange,
    latest_time: 'now',
  });
  const results = entityCentricAlertSearchJob.getResults();

  return results.toPromise().then(
    response => ({ response }),
    err => ({
      status: err.status,
      message: err.statusText,
    }),
  );
};

export const getAlert = alertName => requestAPI(BASE_URL, alertName, 'GET');

export const addNewAlert = data => requestAPI(BASE_URL, '', 'POST', { ...data });

export const updateAlert = (data) => {
  const { name, ...otherData } = data;
  return requestAPI(BASE_URL, name, 'POST', { ...otherData });
};

export const deleteAlert = (data) => {
  const alertName = data;
  return requestAPI(BASE_URL, alertName, 'DELETE');
};

// json_cols is the expected format of data points for charting
export const postSearchAndGetData = (search, earliest) => requestAPI(
  CHART_BASE_URL, '', 'POST', {
    search,
    exec_mode: 'oneshot',
    output_mode: 'json_cols',
    time_format: '%s',
    latest_time: 'now',
    earliest_time: earliest,
    count: 0,
    offset: 0,
  });


/**
 *
 * fetch searches for thre provided sid.
 */
function getAnnotationData({ sid, managedById, managedByType, entityTitle, earliestTime, latestTime = 'now' }) {
  const annotationData = SearchJob.create({
    search: `index="infra_alerts"
    | rex field=_raw "managed_by_id=\\"(?<managed_by_id>.*?)\\","
    | rex field=_raw "entity_id=\\"(?<entity_id>.*?)\\","
    | where entity_title="${entityTitle}" AND managed_by_id="${managedById}" AND managed_by_type="${managedByType}" AND ss_id="${sid}" AND current_state IN (${ALERT_SEVERITY_LEVEL_BY_KEY.WARNING}, ${ALERT_SEVERITY_LEVEL_BY_KEY.CRITICAL})
    | rename _time as time
    | table time, current_value, current_state ss_id`,
    earlist_time: earliestTime,
    latest_time: latestTime,
  });

  const results = annotationData.getResults();
  return results.toPromise().then(
    response => ({ response }),
    err => ({
      status: err.status,
      message: err.statusText,
    }),
  );
}

/**
 * Receives a map of searchId and searchStrings and requests annotation for the sid.
 * Uses promises to sequentially resolve searches
 * @param {sid: "search string"} searchMap
 */
export function getAnnotationDataForSearches(searchMap) {
  const keys = Object.keys(searchMap);
  const promises = keys.map(sid => getAnnotationData({ sid, ...searchMap[sid] }));
  // sequentially process the promise results
  return promises.reduce((chain, p) =>
    chain.then(obj => p.then(res => ({ [keys.shift()]: res.response.results, ...obj }))
      .catch(() => ({ [keys.shift()]: [], ...obj }))), Promise.resolve({}));
}



