import {
  ALERT_CREATE,
  START,
  SUCCESS,
  FAILURE,
} from '../actionTypes';
import ActionUtils from '../actionUtils';

export const ALERT_CREATE_TRACKER = ActionUtils.createRequestTypes(ALERT_CREATE);

export const alertCreateTracker = {
  start: () => ActionUtils.createAction(ALERT_CREATE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(ALERT_CREATE_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(ALERT_CREATE_TRACKER[FAILURE], { error }),
};

export const createAlert = data => ({
  type: ALERT_CREATE,
  data,
});



