import React from 'react';
import PropTypes from 'prop-types';
import { map } from 'lodash';
import Clock from '@splunk/react-icons/Clock';
import ButtonGroup from '@splunk/react-ui/ButtonGroup';
import IconDropdown from './IconDropdown';

export const rangeChoices = {
  '-15m': 'Last 15 minutes',
  '-30m': 'Last 30 minutes',
  '-1h': 'Last 1 hour',
  '-3h': 'Last 3 hours',
  '-6h': 'Last 6 hours',
  '-12h': 'Last 12 hours',
  '-24h': 'Last 24 hours',
  '-48h': 'Last 48 hours',
  '-7d': 'Last 7 days',
  '-30d': 'Last 30 days',
};

class TimeRange extends React.Component {
  static propTypes = {
    onTimeRangeChanged: PropTypes.func.isRequired,
    alertTimeRange: PropTypes.oneOf(Object.keys(rangeChoices)).isRequired,
  };

  constructor() {
    super();
    this.state = {};
  }

  handleChange = (value) => {
    if (rangeChoices[value]) {
      this.props.onTimeRangeChanged(value);
      this.setState({
        selectedRange: value,
      });
    }
  };

  render() {
    const timeRangeChoices = map(rangeChoices, (label, value) => ({ label, value }));
    return (
      <ButtonGroup
        style={{ padding: '0.25rem 0' }}
        aria-label="Time range Menu"
        data-test="time-range-picker"
      >
        <IconDropdown
          dropdownStyle={{
            padding: '0',
            display: 'inline-flex',
            minWidth: 'calc(100% - 32px)',
          }}
          buttonStyle={{ padding: '0', minWidth: 'auto' }}
          data-component-name="select-range"
          icon={<Clock size={1} screenReaderText={null} />}
          items={timeRangeChoices}
          onSelect={this.handleChange}
          appearance="pill"
          selected={this.props.alertTimeRange}
        />
      </ButtonGroup>
    );
  }
}

export default TimeRange;



