import React from 'react';
import PropTypes from 'prop-types';
import { pick } from 'lodash';
import P from '@splunk/react-ui/Paragraph';
import Button from '@splunk/react-ui/Button';
import Dropdown from '@splunk/react-ui/Dropdown';
import Menu from '@splunk/react-ui/Menu';
import More from '@splunk/react-icons/More';
import { _ } from '@splunk/ui-utils/i18n';
import Chart from '../metrics_analysis/Chart';
import { getAppPageUrl } from '../../libs/EMUtils';
import { createTestHook } from '../../util/TestSupport';

export default class AlertSidebarChart extends React.Component {
  static propTypes = {
    chartType: PropTypes.oneOf(['entity', 'group']),
    objectId: PropTypes.string,
    objectFilter: PropTypes.object,
    objectIdDimension: PropTypes.string,
    metricName: PropTypes.string,
    ...Chart.propTypes,
  }

  static defaultProps = {
    chartType: 'entity',
    objectId: '',
    objectFilter: {},
    objectIdDimension: '',
    metricName: '',
    ...Chart.defaultProps,
  }

  constructor(props) {
    super(props);
    this.styles = {
      metricName: {
        paddingTop: '20px',
        paddingLeft: '20px',
        float: 'left',
        fontWeight: '600',
      },
      actionDropdown: {
        padding: '10px 20px',
        float: 'right',
        minWidth: '60px',
      },
      chartContainer: {
        display: 'inline-block',
        width: '100%',
      },
    };
  }

  getAlertUrl = () => {
    const encodedObjectName = encodeURIComponent(this.props.objectId);
    let filters = {};
    if (this.props.chartType === 'entity') {
      filters = {
        [this.props.objectIdDimension]: this.props.objectFilter[this.props.objectIdDimension],
      };
    } else {
      filters = this.props.objectFilter;
    }
    const encodedFilter = encodeURIComponent(JSON.stringify(filters));
    return getAppPageUrl(
      `metrics_analysis?${this.props.chartType}=${encodedObjectName}&filters=${encodedFilter}&alert_name=${this.props.chartName}`,
    );
  }

  render() {
    const mawUrl = this.getAlertUrl();
    const chartProps = pick(this.props, Object.keys(Chart.propTypes));
    const chartAction = (
      <Button
        label={
          <More
            width="20px"
            height="20px"
          />
        }
        appearance="pill"
      />
    );

    return (
      <div
        id={`alert-${this.props.chartName}`}
        {...createTestHook(__filename, name)}
      >
        { !this.props.isLoading &&
          <div style={this.styles.chartContainer}>
            <P style={this.styles.metricName}>{this.props.metricName}</P>
            <Dropdown toggle={chartAction} style={this.styles.actionDropdown}>
              <Menu>
                <Menu.Item to={mawUrl} openInNewContext>
                  {_('Investigate')}
                </Menu.Item>
              </Menu>
            </Dropdown>
            <Chart {...chartProps} />
          </div>
        }
      </div>
    );
  }
}



