import { put, takeLatest, call, select, all } from 'redux-saga/effects';
import { _ } from '@splunk/ui-utils/i18n';
import { includes, join, map, zipObject, forEach, every, filter, reduce } from 'lodash';
import { getEntities } from '../services/entities';
import { getGroups } from '../services/groups';
import { ALERT_SEVERITY_MAPPING, ALERT_SEVERITY_LEVEL_BY_KEY } from '../util/StateConstants';
import { getTimeDifference } from '../util/Common';
import EMUtils from '../libs/EMUtils';
import * as alertsAPI from '../services/alerts';
import {
  ALERT_DELETE,
  ALERT_THRESHOLDS_RETRIEVE,
  ALERT_CHART_DATA_POINTS_RETRIEVE,
  ALERT_CREATE,
  ALERT_EDIT,
  ALERT_RETRIEVE,
  ALERT_LISTING_SORT_ALERTS,
  ALERT_TIME_RANGE_UPDATE,
  ALERT_RETRIEVE_SEARCH,
} from '../actions/actionTypes';
import { alertCreateTracker } from '../actions/alerts/Create';
import { alertEditTracker } from '../actions/alerts/Edit';
import { alertDeleteTracker } from '../actions/alerts/Delete';
import {
  alertRetrieveTracker,
  alertThresholdsRetrieveTracker,
  alertChartDataPointsRetrieveTracker,
  alertRetrieveSearchTracker,
} from '../actions/alerts/Retrieve';
import * as splUtils from '../util/splUtils';
import * as groupUtils from '../util/Groups';


export const getFetchParameters = state => ({
  sortKey: state.alertsReducer.alertSortKey,
  sortDir: state.alertsReducer.alertSortDir,
  timeRange: state.alertsReducer.alertTimeRange,
});

/**
* Parses the severity and counts coming from splunk search
* @param severityInfo -- array of colon seperated severity and counts
* @returns object of severity levels and counts of alerts
*/
function parseSeverityInfo(severityInfo) {
  const parsedSeverity = {};
  for (let i = 0; i < severityInfo.length; i += 1) {
    const split = severityInfo[i].split(':');
    const severityMapped = ALERT_SEVERITY_MAPPING[Number(split[0])];
    if (severityMapped) {
      parsedSeverity[severityMapped] = split[1];
    } else {
      parsedSeverity[split[0]] = split[1];
    }
  }
  return parsedSeverity;
}

function parseAlertResults(results, entities) {
  if (!entities || entities.length === 0) {
    return results.map(result => ({
      ...result,
      severity_info: parseSeverityInfo(result.severity_info),
      last_triggered_time: _(`${getTimeDifference(Number(result.last_triggered_time))}`),
    }));
  }
  return results.map((result) => {
    const extractedDimensions = (entities.find(x => x.name === result.entity_id));
    return {
      ...result,
      last_triggered_time: _(`${getTimeDifference(Number(result.last_triggered_time))}`),
      severity_info: parseSeverityInfo(result.severity_info),
      dimensions: extractedDimensions ? extractedDimensions.dimensions : undefined,
    };
  });
}

function parseAlertEntityIDList(results) {
  return results.map(result => ['_key', result.entity_id]);
}

function parseAlertEntities(entities) {
  if (!entities) {
    return [];
  }
  return entities.map((entity) => {
    const name = entity.name;
    const infoDims = JSON.parse(entity.content.informational_dimensions);
    const idDims = JSON.parse(entity.content.identifier_dimensions);
    return {
      name,
      dimensions: {
        ...infoDims,
        ...idDims,
      },
    };
  });
}

/**
 * Format map with sid and fields for annotation.
 * @param {*} searches
 * @param {*} fetchParams
 */
function getSearchMapForAnnotation(searches, entityTitle, fetchParams) {
  const keys = Object.keys(searches);
  return keys.reduce((acc, sid) => {
    acc[sid] = {
      ...splUtils.getTypeAndIdFromSearch(searches[sid]),
      earliestTime: fetchParams.timeRange,
      entityTitle,
    };
    return acc;
  }, {});
}

/**
 *  Convert the results to annotation format
 * @param {*} annotationResults
 */
function convertAnnotationResultsToChartFormat(annotationResults) {
  const keys = Object.keys(annotationResults);
  return keys.reduce((acc, key) => {
    acc[key] = annotationResults[key].map((result) => {
      const d = new Date(0);
      return {
        value: d.setUTCSeconds(result.time),
        label: parseFloat(result.current_value).toFixed(2),
        color: parseInt(result.current_state, 10) === ALERT_SEVERITY_LEVEL_BY_KEY.WARNING ? '#ed8440' : '#d6563c',
      };
    });
    return acc;
  }, {});
}

/**
 * 
 * @param {Array} dataPoints 
 * @param {string} entityTitle 
 * 
 * filters the list of datapoints for the group to the entityTitle the user
 * currently selected.
 */
function getFilteredDataPoints(dataPoints, entityTitle) {
  const dataPointFields = ['_time', entityTitle];
  const filteredDataPoints = dataPoints.map((data) => {
    if (!data.response.fields || !data.response.columns) {
      return data;
    }
    const fieldIndexes = dataPointFields.map(v => data.response.fields.indexOf(v));
    // Verify all fields are found
    if (fieldIndexes.indexOf(-1) === -1) {
      return {
        response: {
          fields: dataPointFields,
          columns: data.response.columns.filter((v, idx) => fieldIndexes.indexOf(idx) !== -1),
        },
      };
    }
    return data;
  });

  return filteredDataPoints;
}

export function* retrieveAlerts() {
  yield put(alertRetrieveTracker.start());
  try {
    const fetchParams = yield select(getFetchParameters);
    const { response, error } = yield call(alertsAPI.getEntityCentricAlerts,
      fetchParams.sortKey, fetchParams.sortDir, fetchParams.timeRange);
    if (response && response.results) {
      const alertEntityIDList = parseAlertEntityIDList(response.results);
      const queryString = EMUtils.pairsToObjectString(alertEntityIDList);
      // TODO: (ITOA-10706) the status of dimension retrieval is not being tracked
      let entityResponse = yield call(getEntities, { count: 0, offset: 0, fields: 'informational_dimensions,identifier_dimensions' }, queryString);
      entityResponse = entityResponse.response;
      const updated = new Date(entityResponse.updated || Date.now());
      const entities = parseAlertEntities(entityResponse.entry);
      const parsedResults = parseAlertResults(response.results, entities);
      yield put(alertRetrieveTracker.success({ data: parsedResults, updated: updated.getTime() }));
    } else {
      yield put(alertRetrieveTracker.failure(error));
    }
  } catch (error) {
    yield put(alertRetrieveTracker.failure(error));
  }
}

/**
* Helper function to retrieve alerts from alertId,
* -- returns undefined for any error code
*/
function* getAlertsFilterBadResponses(alertId) {
  try {
    const alertResponse = yield call(alertsAPI.getAlert, alertId);
    return alertResponse;
  } catch (error) {
    // return this object because a 500 response indicates a deleted alert
    return {
      alertId,
      status: 'deleted',
    };
  }
}

export function* retrieveSearchesChartDataAnnotations(action) {
  const { entityTitle, alertIDs } = action;
  const fetchParams = yield select(getFetchParameters);
  let returnData = {};
  yield put(alertRetrieveSearchTracker.start());
  try {
    // GET all alerts' data.
    const responses = yield all(
      map(alertIDs, alertId => getAlertsFilterBadResponses(alertId)));
    const activeAlertResponses = filter(responses, r => r.response);
    const deletedAlertResponses = filter(responses, r => r.status === 'deleted');
    // Compose chart data, annotations, and group alerts for successful alert responses
    if (activeAlertResponses.length &&
      every(activeAlertResponses, eachResponse => eachResponse.response.entry[0].content.search)) {
      const activeAlertIDs = map(activeAlertResponses, a => a.response.entry[0].name);
      // Array of 'search' values
      const searches = activeAlertResponses.map(
        eachResponse => eachResponse.response.entry[0].content.search);
      // Get the base search, stripping away threshold values and trigger conditions
      const baseSearches = searches.map(search => splUtils.appendTimeChartIfSplitBy(search));
      // POST each baseSearch with the calculated earliest and latest values
      // the response should be the charting dataPoints for each search
      const dataPoints = yield all(map(
        baseSearches,
        search => call(alertsAPI.postSearchAndGetData, search, fetchParams.timeRange)));
      const alertSPLs = zipObject(activeAlertIDs, searches);
      const annotationSearchMap = getSearchMapForAnnotation(alertSPLs, entityTitle, fetchParams);
      // GET annotations for each search.
      const annotations = yield call(alertsAPI.getAnnotationDataForSearches,
        annotationSearchMap);
      const formattedAnnotations = convertAnnotationResultsToChartFormat(annotations);
      const annotationsValues = activeAlertIDs.reduce((result, id) => {
        result.push(formattedAnnotations[id]);
        return result;
      }, []);
      const filteredDataPoints = getFilteredDataPoints(dataPoints, entityTitle);
      const chartData = zipObject(activeAlertIDs, filteredDataPoints.map((dataPoint, index) => ({
        ...splUtils.pickFieldsColumns(dataPoint.response),
        annotations: annotationsValues[index],
      })));

      // get managed by type and id per alert
      const alertTypes = {};
      const alertManagedByIds = {};
      forEach(annotationSearchMap, (val, alertId) => {
        alertTypes[alertId] = val.managedByType;
        alertManagedByIds[alertId] = val.managedById;
      });
      // get group filters for group alerts
      const groupAlerts = [];
      const groupAlertsManagedByIds = [];
      forEach(alertManagedByIds, (val, alertId) => {
        if (alertTypes[alertId] === 'group') {
          groupAlerts.push(alertId);
          groupAlertsManagedByIds.push(val);
        }
      });
      let alertGroupFilters = {};
      if (groupAlerts.length !== 0) {
        let groupResponse = yield call(
          getGroups,
          { count: 0, offset: 0 },
          { name: groupAlertsManagedByIds },
        );
        let groupFilters = [];
        groupResponse = groupResponse.response;
        if (groupResponse) {
          groupFilters = map(groupResponse.entry, g =>
            groupUtils.groupFilterStringToObject(g.content.filter));
        }
        alertGroupFilters = zipObject(groupAlerts, groupFilters);
      }

      // set alert thresholds
      const alertThresholds = reduce(alertSPLs, (result, alertSpl, alertId) => {
        const res = result;
        res[alertId] = splUtils.getThresholdsInfo(alertSpl);
        return res;
      }, {});

      returnData = {
        alertIDs: activeAlertIDs,
        alertSPLs,
        alertTypes,
        alertManagedByIds,
        alertGroupFilters,
        alertThresholds,
        alertDataPoints: chartData,
      };
    }
    // Compose list of deleted alertIds
    if (deletedAlertResponses.length) {
      returnData.deletedAlertIDs = deletedAlertResponses.map(alert => alert.alertId);
    }
    // Fire success event if returnData has been composed above
    if (returnData.alertIDs || returnData.deletedAlertIDs) {
      yield put(alertRetrieveSearchTracker.success(returnData));
    }
    if (responses.length === 0) {
      yield put(alertRetrieveSearchTracker.failure('Error with alert response.'));
    }
  } catch (error) {
    yield put(alertRetrieveSearchTracker.failure(error));
  }
}

export function* loadAlert(action) {
  const { alertName } = action;
  yield put(alertThresholdsRetrieveTracker.start());
  try {
    const { response, error } = yield call(alertsAPI.getAlert, alertName);
    if (response && response.entry.length === 1) {
      const data = response.entry[0];
      yield put(alertThresholdsRetrieveTracker.success(
        {
          ...splUtils.emailParams(data),
          ...splUtils.getThresholdsInfo(data.content.search),
        },
      ));
    } else {
      yield put(alertThresholdsRetrieveTracker.failure(error));
    }
  } catch (error) {
    yield put(alertThresholdsRetrieveTracker.failure(error));
  }
}

export function* deleteAlert(action) {
  const thresholdName = action.data;
  yield put(alertDeleteTracker.start());
  try {
    const { response, error } = yield call(alertsAPI.deleteAlert, thresholdName);
    if (response && response.entry.length) {
      yield put(alertDeleteTracker.success());
    } else {
      yield put(alertDeleteTracker.failure(error));
    }
  } catch (error) {
    yield put(alertDeleteTracker.failure(error));
  }
}

export function* createAlert(action) {
  const { warningValue, criticalValue, direction, ...nonThresholdData } = action.data;
  const requestData = {
    ...splUtils.encodeThresholds({ warningValue, criticalValue, direction }),
    ...splUtils.getNonThresholdPayload({ ...nonThresholdData }),
  };
  yield put(alertCreateTracker.start());
  try {
    const { response, error } = yield call(alertsAPI.addNewAlert, requestData);
    if (response && response.entry.length === 1) {
      yield put(alertCreateTracker.success());
    } else {
      yield put(alertCreateTracker.failure(error));
    }
  } catch (error) {
    yield put(alertCreateTracker.failure(error));
  }
}

export function* editAlert(action) {
  const { warningValue, criticalValue, direction, ...nonThresholdData } = action.data;
  const requestData = {
    ...splUtils.encodeThresholds({ warningValue, criticalValue, direction }),
    ...splUtils.getNonThresholdPayload({ ...nonThresholdData }),
  };
  yield put(alertEditTracker.start());
  try {
    const { response, error } = yield call(alertsAPI.updateAlert, requestData);
    if (response && response.entry) {
      yield put(alertEditTracker.success());
    } else {
      yield put(alertEditTracker.failure(error));
    }
  } catch (error) {
    yield put(alertEditTracker.failure(error));
  }
}

export function* loadChartDataPoints(action) {
  let { search } = action;
  const { earliest } = action;
  yield put(alertChartDataPointsRetrieveTracker.start());
  try {
    if (includes(search, 'untable')) {
      const untabledSearch = search.split('|');
      untabledSearch.splice(untabledSearch.length - 1, 1);
      search = join(untabledSearch, '|');
    }
    const { response, error } = yield call(
      alertsAPI.postSearchAndGetData, search, earliest);
    if (response && response.fields.length && response.columns.length) {
      const results = splUtils.pickFieldsColumns(response);
      yield put(alertChartDataPointsRetrieveTracker.success(results));
    } else {
      yield put(alertChartDataPointsRetrieveTracker.failure(error));
    }
  } catch (error) {
    yield put(alertChartDataPointsRetrieveTracker.failure(error));
  }
}

export function* alertSaga() {
  yield takeLatest(ALERT_DELETE, deleteAlert);
  yield takeLatest(ALERT_THRESHOLDS_RETRIEVE, loadAlert);
  yield takeLatest(ALERT_CHART_DATA_POINTS_RETRIEVE, loadChartDataPoints);
  yield takeLatest(ALERT_CREATE, createAlert);
  yield takeLatest(ALERT_EDIT, editAlert);
  yield takeLatest(ALERT_RETRIEVE, retrieveAlerts);
  yield takeLatest(ALERT_LISTING_SORT_ALERTS, retrieveAlerts);
  yield takeLatest(ALERT_TIME_RANGE_UPDATE, retrieveAlerts);
  yield takeLatest(ALERT_RETRIEVE_SEARCH, retrieveSearchesChartDataAnnotations);
}



