import React from 'react';
import PropTypes from 'prop-types';
import { _ } from '@splunk/ui-utils/i18n';
import P from '@splunk/react-ui/Paragraph';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import { map } from 'lodash';
import AlertSidebarChart from './AlertSidebarChart';
import { FETCH_OBJECT_STATE } from '../../util/StateConstants';

export default class AlertSidebarChartArea extends React.Component {
  static propTypes = {
    /** SPL for each chart */
    alertSPLs: PropTypes.object,
    /** data points for each chart */
    alertDataPoints: PropTypes.object,
    /** thresholds for each alert */
    alertThresholds: PropTypes.object,
    /** managed by type for each alert */
    alertTypes: PropTypes.object,
    /** managed by id for each alert */
    alertManagedByIds: PropTypes.object,
    /** group filter for each group alert */
    alertGroupFilters: PropTypes.object,
    /** metric name for each alert */
    alertMetricNames: PropTypes.object,
    /** alert ids */
    alertIDs: PropTypes.arrayOf(PropTypes.string),
    /** deleted alert ids */
    deletedAlertIDs: PropTypes.arrayOf(PropTypes.string),
    /** sidebar fetch status */
    alertSideBarDataFetchState: PropTypes.string,
    /** Alert sidebar chart related props */
    ...AlertSidebarChart.propTypes,
  }

  static defaultProps = {
    alertSPLs: {},
    alertDataPoints: {},
    alertThresholds: {},
    alertIDs: [],
    alertManagedByIds: {},
    alertGroupFilters: {},
    alertMetricNames: {},
    alertTypes: {},
    deletedAlertIDs: [],
    ...AlertSidebarChart.defaultProps,
  }

  constructor(props) {
    super(props);
    this.state = {
      alertThresholds: {},
      showLoading: true,
    };
    this.styles = {
      chartsArea: {
        height: 'calc(90%)',
        overflow: 'auto',
      },
      chart: {
        padding: '20px 10px',
      },
      waitSpinner: {
        width: '100%',
        margin: '10px auto',
      },
    };
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      showLoading: !(nextProps.alertSideBarDataFetchState === FETCH_OBJECT_STATE.SUCCESS ||
      nextProps.alertSideBarDataFetchState === FETCH_OBJECT_STATE.ERROR),
    });
  }

  render() {
    return (
      <div style={this.styles.chartsArea}>
        {this.state.showLoading &&
          <WaitSpinner style={this.styles.waitSpinner} />
        }
        {
          map(this.props.alertIDs, (alertId) => {
            const objectFilter = this.props.alertTypes[alertId] === 'entity' ?
              this.props.objectFilter :
              this.props.alertGroupFilters[alertId];
            return (
              <AlertSidebarChart
                style={this.styles.chart}
                isLoading={this.state.showLoading}
                key={alertId}
                chartName={alertId}
                chartType={this.props.alertTypes[alertId]}
                objectId={this.props.alertManagedByIds[alertId]}
                objectIdDimension={this.props.objectIdDimension}
                objectFilter={objectFilter}
                chartData={this.props.alertDataPoints[alertId]}
                plotBandsData={this.props.alertThresholds[alertId]}
                metricName={this.props.alertMetricNames[alertId]}
              />
            );
          })
        }
        {
          map(this.props.deletedAlertIDs, (alertId) => {
            if (!this.state.showLoading) {
              return (
                <div key={alertId} style={{ paddingLeft: '20px', paddingTop: '10px' }}>
                  <P style={{ fontWeight: '600' }}>{this.props.alertMetricNames[alertId]}</P>
                  <P>{_('Alert has been deleted.')}</P>
                </div>
              );
            }
            return '';
          })
        }
      </div>
    );
  }
}



