import {
  ALERT_EDIT,
  START,
  SUCCESS,
  FAILURE,
} from '../actionTypes';
import ActionUtils from '../actionUtils';

export const ALERT_EDIT_TRACKER = ActionUtils.createRequestTypes(ALERT_EDIT);

export const alertEditTracker = {
  start: () => ActionUtils.createAction(ALERT_EDIT_TRACKER[START], {}),
  success: response => ActionUtils.createAction(ALERT_EDIT_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(ALERT_EDIT_TRACKER[FAILURE], { error }),
};

export const editAlert = data => ({
  type: ALERT_EDIT,
  data,
});



