import _ from 'lodash';
import { normalizeBoolean } from '@splunk/ui-utils/boolean';
import { THRESHOLD_DIRECTION, EMAIL_WHEN } from '../util/StateConstants';
import { dimensionObjectsToFilterString } from './Dimensions';


const SPL_KV_REGEX = /((?:"\w"|\w)+)=((?:"[^"]*"|(?!mvindex)(?!if)[^=,| ])+)/ig;
const SPL_PIPE_DELIMETER = '|';

/**
 *
 * @param {*} str
 *
 * converts test_with_underscore to testWithUnderscore
 */
function toCamelCase(str) {
  return str.replace(/(_[a-zA-Z0-9])/g, item => item[1].toUpperCase() + item.substr(2));
}

export function decodeThresholds(data) {
  const {
    infoMin,
    infoMax,
    warningMin,
    warningMax,
    criticalMin,
    criticalMax,
  } = data;
  let warningValue = null;
  let criticalValue = null;
  let direction;

  if (criticalMin > warningMin) {
    direction = THRESHOLD_DIRECTION.NORMAL;
    if (criticalMax === criticalMin) {
      warningValue = warningMin;
    } else if (infoMin === infoMax) {
      criticalValue = criticalMin;
    } else {
      warningValue = warningMin;
      criticalValue = criticalMin;
    }
  } else {
    direction = THRESHOLD_DIRECTION.REVERSE;
    if (criticalMax === criticalMin) {
      warningValue = warningMax;
    } else if (infoMin === infoMax) {
      criticalValue = criticalMax;
    } else {
      warningValue = warningMax;
      criticalValue = criticalMax;
    }
  }

  return {
    warningValue: warningValue ? warningValue.toString() : '',
    criticalValue: criticalValue ? criticalValue.toString() : '',
    direction,
  };
}

/**
 *
 * @param {spl string} spl
 *
 * returns thre base search;
 */
export function getBaseSearch(spl) {
  let trimmedSpl = spl.trim();
  if (trimmedSpl.indexOf(SPL_PIPE_DELIMETER) === 0) {
    trimmedSpl = trimmedSpl.substr(1).trim();
  }

  return `| ${trimmedSpl.split(SPL_PIPE_DELIMETER)[0]}`;
}

/**
 *
 * @param {spl string} spl
 *
 * returns a map of all the key=value pairs in the spl string
 */
export function getAllSearchKVMap(spl) {
  if (!SPL_KV_REGEX.test(spl)) {
    return null;
  }

  return spl.match(SPL_KV_REGEX).reduce((acc, i) => {
    const item = i.split(/=(.+)/);
    let key = item[0];
    let value = item[1].replace(/["]/g, '');
    if (item[0].startsWith('threshold_')) {
      key = key.replace('threshold_', '');
      value = parseFloat(value, 10);
    }
    key = toCamelCase(key);
    acc[key] = value;
    return acc;
  }, {});
}

/**
 *
 * @param {spl string} spl
 *
 * Helper function to return the decoded thresholds from search
 */
export function getThresholdsInfo(spl) {
  const map = getAllSearchKVMap(spl);
  return decodeThresholds(map);
}


/**
 * Helper function to fetch the managedById and managedByType
 * @param {*} spl
 */
export function getTypeAndIdFromSearch(spl) {
  const data = getAllSearchKVMap(spl);
  const keys = ['managedById', 'managedByType'];
  if (keys.every(key => Object.prototype.hasOwnProperty.call(data, key))) {
    return keys.reduce((acc, val) => {
      acc[val] = data[val];
      return acc;
    }, {});
  }
  return null;
}

/**
 *
 * @param {spl string} spl
 *
 * return the MAW transform format.
 */
export function getMawTransforms(spl) {
  const result = [];
  const map = getAllSearchKVMap(spl);
  if (!map || !map.aggregationMethod) {
    return [];
  }

  const thresholds = decodeThresholds(map);
  // warning;
  result.push({
    value: thresholds.warningValue,
    aggregation: map.aggregationMethod,
    comparator: thresholds.direction === THRESHOLD_DIRECTION.NORMAL ? '>' : '<',
    severity: 3,
  });

  // critical
  result.push({
    value: thresholds.criticalValue,
    aggregation: map.aggregationMethod,
    comparator: thresholds.direction === THRESHOLD_DIRECTION.NORMAL ? '>' : '<',
    severity: 5,
  });

  return result;
}

export function getEarliestLatest(fromTime) {
  const nowSeconds = Date.now() / 1000;
  const timeDiff = parseInt(fromTime.slice(1, -1), 10);
  let thenSeconds;

  switch (fromTime[fromTime.length - 1]) {
    case 'm':
      thenSeconds = nowSeconds - (timeDiff * 60);
      break;
    case 'h':
      thenSeconds = nowSeconds - (timeDiff * 60 * 60);
      break;
    case 'd':
      thenSeconds = nowSeconds - (timeDiff * 24 * 60 * 60);
      break;
    default:
      thenSeconds = nowSeconds;
      break;
  }

  return { earliest: thenSeconds, latest: nowSeconds };
}

function getSpan(fromTime) {
  /*
    https://git.splunk.com/projects/SCRD/repos/data-analysis/browse/src/utils
    /span-calculator.js
  */
  switch (fromTime) {
    case '-15m':
    case '-30m':
    case '-1h':
    case '-3h':
    case '-6h':
      return '1m';
    case '-12h':
      return '2m';
    case '-24h':
    case '-48h':
      return '5m';
    case '-7d':
      return '30m';
    case '-30d':
      return '2h';
    default: // default time range is 'Last 1 hr'
      return '10s';
  }
}

export function buildSearchString(measure, title, tagFilters,
  metricFilters, aggregations, earliest, splitBy) {
  /*
    aggregations: ["avg", "max", ...]
    measure: "cpu.system"
    splitBy: {dimension: "os_version", limitType: "max", limit: 200}
    title: hostname (EAW)
    urlParams: { filters: ["{"tag":["planets"],"server":["production"]}"] } (GAW)

    examples:
    | mstats avg(_value) as "Avg" WHERE "host"="alaska.usa.com" AND
    metric_name="cpu.system" earliest=1520287735.456 latest=1520291335.456
    span=10s BY "os_version"

    | mstats avg(_value) as "Avg" WHERE "tag"="planets" AND "server"="production" AND
    metric_name="swap.swap_io.in" earliest=1520361098.103 latest=1520364698.103
    span=10s BY "os_version"

    | mstats avg(_value) as "Avg" WHERE "host"="alabama.usa.com" AND
    ("cpu"="0" OR "cpu"="1" OR "cpu"="2" OR "cpu"="5") AND metric_name="cpu.system"
    earliest=1520879211.197 latest=1520882811.197 span=10s BY "cpu"
  */
  let splitBySearch;
  let whereSearch;
  let filterSearch;
  let timeChartSearch;

  const span = getSpan(earliest);
  const aggrCollection = aggregations.map(
    each => `${each}(_value) as "${each.replace(/^./, each[0].toUpperCase())}"`);
  const aggregationSearch = `| mstats ${aggrCollection.join(' ')}`;

  if (metricFilters) {
    let dimension;
    let values;
    filterSearch = _.map(metricFilters, (filter) => {
      ({ dimension, values } = filter);
      return `(${_.map(values, value => `"${dimension}"="${value}"`).join(' OR ')})`;
    }).join(' AND ');
  }

  // Call came from EAW
  if (title) {
    if (measure.includes('AWS/EC2')) {
      whereSearch = `WHERE "InstanceId"="${title}" AND metric_name="${measure}"`;
    } else {
      whereSearch = `WHERE "host"="${title}" AND ${filterSearch && `${filterSearch} AND `}
        metric_name="${measure}"`;
    }
  } else if (tagFilters) {
    // Call came from GAW
    const parsedJson = _.transform(JSON.parse(tagFilters[0]), (result, val, key) => {
      result.push(val.map(each => `"${key}"="${each}"`).join(' AND '));
    }, []).join(' AND ');
    whereSearch = `WHERE ${parsedJson} AND ${filterSearch && `${filterSearch} AND `}
      metric_name="${measure}"`;
  }

  // splitBy is not available when multiple aggregations are selected
  if (aggregations.length === 1 && splitBy) {
    // pipe results of mstats search through a timechart search to get the desired data
    const aggr = aggregations[0].replace(/^./, aggregations[0][0].toUpperCase());
    timeChartSearch = `| timechart first("${aggr}") as "${aggr}" agg=${splitBy.limitType}
      limit=${splitBy.limit} useother=false span=${span} BY "${splitBy.dimension}"`;
    switch (splitBy.limitType) {
      // default is max.
      case 'min':
        splitBySearch = `BY "${splitBy.dimension}" | tail ${splitBy.limit}`;
        break;
      default:
        splitBySearch = `BY "${splitBy.dimension}" | head ${splitBy.limit}`;
        break;
    }
  } else {
    splitBySearch = '';
    timeChartSearch = '';
  }

  const timeRange = getEarliestLatest(earliest);
  const timeRangeSearch = `earliest=${timeRange.earliest} latest=${timeRange.latest}`;
  const spanSearch = `span=${span}`;

  return `${aggregationSearch} ${whereSearch} ${timeRangeSearch}
    ${spanSearch} ${splitBySearch} ${timeChartSearch}`;
}

export function encodeThresholds({ warningValue, criticalValue, direction }) {
  let infoMin = null;
  let infoMax = null;
  let warningMin = null;
  let warningMax = null;
  let criticalMin = null;
  let criticalMax = null;

  const numWarning = +warningValue;
  const numCritical = +criticalValue;

  // direction = NORMAL : greater than
  // direction = REVERSE : lesser than
  if (direction === THRESHOLD_DIRECTION.REVERSE) {
    if (numWarning && numCritical) {
      warningMax = numWarning;
      criticalMax = numCritical;
      warningMin = numCritical;
      criticalMin = Number.MIN_SAFE_INTEGER;
      infoMax = Number.MAX_SAFE_INTEGER;
      infoMin = numWarning;
    } else if (numWarning) {
      warningMin = Number.MIN_SAFE_INTEGER;
      criticalMax = Number.MIN_SAFE_INTEGER;
      criticalMin = Number.MIN_SAFE_INTEGER;
      warningMax = numWarning;
      infoMax = Number.MAX_SAFE_INTEGER;
      infoMin = numWarning;
    } else if (numCritical) {
      criticalMin = Number.MIN_SAFE_INTEGER;
      criticalMax = numCritical;
      warningMin = numCritical;
      warningMax = Number.MAX_SAFE_INTEGER;
      infoMin = Number.MAX_SAFE_INTEGER;
      infoMax = Number.MAX_SAFE_INTEGER;
    }
  } else if (direction === THRESHOLD_DIRECTION.NORMAL) {
    if (numWarning && numCritical) {
      warningMin = numWarning;
      criticalMin = numCritical;
      warningMax = numCritical;
      criticalMax = Number.MAX_SAFE_INTEGER;
      infoMin = Number.MIN_SAFE_INTEGER;
      infoMax = numWarning;
    } else if (numWarning) {
      warningMin = numWarning;
      warningMax = Number.MAX_SAFE_INTEGER;
      infoMin = Number.MIN_SAFE_INTEGER;
      infoMax = numWarning;
      criticalMin = Number.MAX_SAFE_INTEGER;
      criticalMax = Number.MAX_SAFE_INTEGER;
    } else if (numCritical) {
      warningMin = Number.MIN_SAFE_INTEGER;
      warningMax = numCritical;
      criticalMin = numCritical;
      criticalMax = Number.MAX_SAFE_INTEGER;
      infoMin = Number.MIN_SAFE_INTEGER;
      infoMax = Number.MIN_SAFE_INTEGER;
    }
  }

  // when editing existing thresholds, toggling the email checkbox to false
  // set email Recipients to none and email condition to none.

  return {
    info_min: infoMin,
    info_max: infoMax,
    warning_min: warningMin,
    warning_max: warningMax,
    critical_min: criticalMin,
    critical_max: criticalMax,
  };
}

export function getNonThresholdPayload({
  emailTo,
  emailWhen,
  emailEnabled,
  typeId,
  objectType,
  alertSpl,
  name,
}) {
  return {
    name,
    managed_by_id: typeId,
    managed_by_type: objectType,
    metric_spl: getBaseSearch(alertSpl),
    email_enabled: emailEnabled,
    email_to: emailEnabled ? emailTo : '',
    email_when: emailEnabled ? emailWhen : EMAIL_WHEN.IMPROVE_OR_DEGRADE,
  };
}
export function emailParams(getResponse) {
  /*
    get the following params:
    email_to, email_when, email_enabled, name (of the alert), typeId (entity/ group ID)
  */
  return {
    emailEnabled: normalizeBoolean(getResponse.content['action.em_send_email']),
    emailTo: getResponse.content['action.em_send_email.param.email_to'] || '',
    emailWhen: getResponse.content['action.em_send_email.param.email_when'] || EMAIL_WHEN.IMPROVE_OR_DEGRADE,
    name: getResponse.name,
    typeId: getResponse.content['alert.managedBy'].split(':')[1],
  };
}

/**
 * replaces filters in an spl with the "group_filter" macro
 * @param {String} spl spl that contains the filter
 * @param {Object} filters filters to be replaced in the spl -- key, value pair
 * @param {String} groupId argument of the "group_filter" macro
 * @returns {String} SPL after replacement
 */
export function replaceGroupFilterWithMacro(spl, filters, groupId) {
  const filterRegexp = /"\S+"="\S+"/g;
  function replaceIfMatch(match) {
    let [dim, val] = match.split('=');
    dim = dim.replace(/^"(.*)"$/, '$1');
    val = val.replace(/^"(.*)"$/, '$1');
    if (filters[dim] !== undefined && filters[dim].includes(val)) {
      return '<mark>';
    }
    return match;
  }
  const intermediateSpl = spl.replace(filterRegexp, replaceIfMatch);
  const markRegex = /\(?<mark.*>\)?/g;
  const finalSpl = intermediateSpl.replace(markRegex, `[\`group_filter(${groupId})\`]`);
  return finalSpl;
}

/**
 * expands macro with dimensions filter
 * @param {String} spl spl that contains the macro to be expanded
 * @param {Object} filters dimensions that are used to replace the macro
 * @returns {String} SPL after expansion
 */
export function expandGroupFilterMacroWithDimensions(spl, filters) {
  const macroRegexp = /\[`group_filter\(.*\)`\]/g;
  const replacementFilter = dimensionObjectsToFilterString(filters);
  return spl.replace(macroRegexp, replacementFilter);
}

/**
 * picks fields and columns only from the response of a rest call.
 * removes _span, if such a field exists
 * @param {Object} restResponse response from a rest call to search job endpoint.
 * @returns {Object} {fields: [...], columns: [...]}
 */
export function pickFieldsColumns(restResponse) {
  const results = _.pick(restResponse, ['fields', 'columns']);
  const indexOfSpan = _.indexOf(results.fields, '_span');
  if (indexOfSpan > -1) {
    results.fields.splice(indexOfSpan, 1);
    results.columns.splice(indexOfSpan, 1);
  }
  return results;
}

export function appendTimeChartIfSplitBy(search) {
  const baseSearch = getBaseSearch(search);
  if (_.includes(baseSearch, 'BY')) {
    // Search has the splitBy option
    const spanIndex = baseSearch.indexOf('span');
    const kvMap = getAllSearchKVMap(search);
    const timeChartSearch = `| timechart first("${kvMap.aggregationMethod}") as "${kvMap.aggregationMethod}" ${baseSearch.slice(spanIndex)} limit=0`;
    return `${baseSearch} ${timeChartSearch}`;
  }
  return baseSearch;
}



